<template>
  <div
    id="productModal"
    ref="modal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 id="productModalLabel" class="modal-title">
            <span></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-1">
                <div class="form-group">
                  <label for="imageUrl"></label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="productObj.imageUrl"
                  />
                </div>
                <div class="m-2 d-block text-center"></div>
                <label class="d-block btn btn-primary text-black mb-4">
                  <input
                    style="display:none"
                    @change="upload('main', $event)"
                    type="file"
                    class="form-control"
                    id="file"
                    placeholder=""
                  />
                  <div class="text-center"></div>
                </label>
                <img class="img-fluid" :src="productObj.imageUrl" alt="" />
              </div>
              <!-- -->
              <div class="text-secondary mb-2"></div>
              <div v-for="(item, index) in productObj.imagesUrl" :key="index">
                <input
                  type="text"
                  class="border-0 text-secondary border-bottom border-primary-light
                  bg-light w-100 p-2 mb-2"
                  placeholder=""
                  v-model="productObj.imagesUrl[index]"
                />

                <img class="img-fluid mb-2" :src="productObj.imagesUrl[index]" />
              </div>
              <div
                v-if="
                  !productObj.imagesUrl.length ||
                    productObj.imagesUrl[productObj.imagesUrl.length - 1]
                "
              >
                <button v-on:click="addImage" class="btn btn-outline-primary btn-sm d-block w-100">
                  
                </button>
              </div>
              <div v-else>
                <button v-on:click="delImage" class="btn btn-outline-danger btn-sm d-block w-100">
                  
                </button>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  id="title"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model.trim="productObj.title"
                />
              </div>
              <div class="row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    id="category"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model.trim="productObj.category"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input
                    id="unit"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model.trim="productObj.unit"
                  />
                </div>
              </div>
              <div class="row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    id="origin_price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model.number="productObj.origin_price"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model.number="productObj.price"
                  />
                </div>
              </div>
              <hr />

              <div class="form-group">
                <label for="content"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model.trim="productObj.content"
                ></textarea>
              </div>
              <table class="table">
                <thead>
                  <tr>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th width="15%" scope="col">
                      <button @click="addSize" type="button" class="btn btn-sm btn-secondary">
                        
                      </button>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in productObj.size" :key="index">
                    <td>
                      <input
                        type="number"
                        class="text-center"
                        v-model.trim="productObj.size[index].sizeNum"
                      />
                    </td>
                    <td>
                      <input
                        type="number"
                        class="text-center"
                        min="1"
                        v-model.trim="productObj.size[index].sizeQty"
                      />
                    </td>
                    <td></td>
                  </tr>
                </tbody>
              </table>
              <!-- <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model.trim="productObj.size[0].sizeNum"
                ></textarea>
              </div>
              <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model.trim="productObj.size[0].sizeQty"
                ></textarea>
              </div>-->
              <div class="form-group">
                <div class="form-check">
                  <input
                    id="is_enabled"
                    class="form-check-input"
                    type="checkbox"
                    :value="productObj.is_enabled"
                    v-model="productObj.is_enabled"
                    :checked="productObj.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled"></label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            
          </button>
          <button @click="updateProduct" type="button" class="btn btn-primary">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
  data() {
    return {
      modal: '',
      productObj: {
        imagesUrl: [],
        size: [],
      },
      buttomStatus: '',
    };
  },
  methods: {
    addSize() {
      if (this.productObj.size === '') {
        this.productObj.size = [];
      }
      if (Object.keys(this.productObj.size[this.productObj.size.length - 1]).length !== 0) {
        this.productObj.size.push({});
      }
    },
    upload(key, e) {
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', file);
      this.$http
        .post(`${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`, formData)
        .then((res) => {
          this.productObj.imageUrl = res.data.imageUrl;
          console.log(res);
        })
        .catch((res) => {
          console.log(res);
        });
    },
    addImage() {
      if (this.productObj.imagesUrl === '') {
        this.productObj.imagesUrl = [];
      }
      if (this.productObj.imagesUrl[this.productObj.imagesUrl.length - 1] !== '') {
        console.log('yes');
        this.productObj.imagesUrl.push('');
      }
    },
    delImage() {
      this.productObj.imagesUrl.splice(this.productObj.imagesUrl.length - 1, 1);
    },
    updateProduct() {
      if (this.buttomStatus === '') {
        this.$http
          .post(`${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`, {
            data: this.productObj,
          })
          .then((res) => {
            if (res.data.success) {
              this.$swal(`${res.data.message}`);
              this.$emit('refresh');
              this.modal.hide();
            } else {
              this.$swal(`${res.data.message}`);
            }
          })
          .catch((res) => {
            this.$swal(`${res.data.message}`);
          });
      } else if (this.buttomStatus === '') {
        this.$http
          .put(
            `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.productObj.id}`,
            { data: this.productObj },
          )
          .then((res) => {
            if (res.data.success) {
              this.$swal(`${res.data.message}`);
              this.$emit('refresh');
              this.modal.hide();
              console.log(this.productObj);
            } else {
              this.$swal(`${res.data.message}`);
            }
          })
          .catch((res) => {
            this.$swal(`${res.data.message}`);
          });
      }
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.modal);
  },
};
</script>
